body {
  margin: 0;
  padding: 0;
}

.stars {
  display: flex;
  height: 15px;
}

.stars__star {
  width: 15px;
  margin-right: 4px;
  background: no-repeat url("images/star.svg") 50%;
}

.stars--1 .stars__star:nth-child(1),
.stars--2 .stars__star:nth-child(-n+2),
.stars--3 .stars__star:nth-child(-n+3),
.stars--4 .stars__star:nth-child(-n+4),
.stars--5 .stars__star:nth-child(-n+5) {
  background: no-repeat url("images/star-active.svg") 50%;
}
